<template>
	<div id="store-cart">
		<template v-if="show == 1">
			<template v-if="!IN_MALL">
				<template v-if="istore.is_rest == 0">
					<template v-if="from == 'table'">
						<div class="rest" v-if="istore.is_meal != 1">店内点餐暂未开启</div>
						<div class="rest" v-else-if="!table">桌号不存在</div>
						<div class="rest" v-else-if="table && table.status > 1 && (!table.jiacai_id || table.jiacai_id <= 0)">此桌已开台</div>
					</template>
					<div class="cart">
						<template v-if="from == 'table'">
							<div class='cart-tips' v-if="table && (table.status == 1 || table.jiacai_id > 0)">
								<div class="cart-icon" ref="cart_icon" :class="{active: cartTotalNum > 0}" @click="popupCart = (!popupCart && icart && icart.num > 0)">
									<van-icon name="cart1"></van-icon>
									<div class="cart-num" v-if="cartTotalNum > 0">{{cartTotalNum}}</div>
								</div>
								<div class='cart-empty' v-if="cartTotalNum == 0">
									购物车空空如也~~
								</div>
								<div class="cart-noempty" v-else-if="cartTotalNum > 0">
									<div class="cart-price">{{Lang.dollarSign}}{{cartTotalPrice}}</div>
								</div>
							</div>
							<div class='cart-btns' v-if="table">
								<div class='cart-btn-unavail'>
									<div class="inner" v-if="!cartTotalNum || cartTotalNum == 0">未点菜</div>
								</div>
								<div @click="util.jspost({url: 'wmall/store/table/jiacai', data: {order_id: table.order_id, sid: istore.id}, confirm: '确定加菜吗?', successUrl: '/pages/order/index'})" class="cart-btn-confirm" v-if="table.jiacai_id > 0">
									<div class="inner">加菜</div>
								</div>
								<router-link :to="util.getUrl({'path': '/tangshi/pages/table/pindan', query: {sid: istore.id, table_id: table.id, pindan_id: pindan_id}})" class="cart-btn-confirm" v-if="pindan_id > 0">
									<div class="inner">选好了</div>
								</router-link>
								<router-link :to="util.getUrl({'path': '/tangshi/pages/table/create', query: {sid: istore.id, table_id: table.id}})" class="cart-btn-confirm" v-if="cartSendCondition <= 0">
									<div class="inner">去结算</div>
								</router-link>
							</div>
						</template>
						<template v-else-if="from == 'reserve'">
							<div class='cart-tips'>
								<div class="cart-icon" ref="cart_icon" :class="{active: cartTotalNum > 0}" @click="popupCart = (!popupCart && icart && icart.num > 0)">
									<van-icon name="cart1"></van-icon>
									<div class="cart-num" v-if="cartTotalNum > 0">{{cartTotalNum}}</div>
								</div>
								<div class='cart-empty' v-if="cartTotalNum == 0">
									购物车空空如也~~
								</div>
								<div class="cart-noempty" v-else-if="cartTotalNum > 0">
									<div class="cart-price">{{Lang.dollarSign}}{{cartTotalPrice}}</div>
								</div>
							</div>
							<div class='cart-btns'>
								<div class='cart-btn-unavail'>
									<div class="inner" v-if="cartSendCondition > 0">还差{{Lang.dollarSign}}{{cartSendCondition}}</div>
								</div>
								<router-link :to="util.getUrl({'path': '/tangshi/pages/reserve/create', query: {sid: this.istore.id}})" class="cart-btn-confirm" v-if="cartSendCondition <= 0">
									<div class="inner">去结算</div>
								</router-link>
							</div>
						</template>
						<template v-else>
							<div class='cart-tips'>
								<div class="cart-icon" ref="cart_icon" :class="{active: cartTotalNum > 0}" @click="popupCart = (!popupCart && icart && icart.num > 0)">
									<van-icon name="cart1"></van-icon>
									<div class="cart-num" v-if="cartTotalNum > 0">{{cartTotalNum}}</div>
								</div>
								<div class='cart-empty' v-if="cartTotalNum == 0">
									购物车空空如也~~
								</div>
								<div class="cart-noempty" v-else-if="cartTotalNum > 0">
									<div class="cart-price">{{Lang.dollarSign}}{{cartTotalPrice}}</div>
								</div>
							</div>
							<div class='cart-btns'>
								<template v-if="pindan_id > 0">
									<div class='cart-btn-unavail'>
										<div class="inner" v-if="!cartTotalNum || cartTotalNum == 0">未选择商品</div>
									</div>
									<div @click="onSubmit()" class="cart-btn-confirm">
										<div class="inner">选好了</div>
									</div>
								</template>
								<template v-else>
									<div class='cart-btn-unavail'>
										<div class="inner" v-if="!cartTotalNum || cartTotalNum == 0">{{Lang.dollarSign}}{{cartSendCondition}}起送</div>
										<div class="inner" v-else-if="cartSendCondition > 0">差{{Lang.dollarSign}}{{cartSendCondition}}起送</div>
									</div>
									<div @click="onSubmit()" class="cart-btn-confirm" v-if="cartSendCondition <= 0">
										<div class="inner">去结算</div>
									</div>
								</template>
							</div>
						</template>
						<div class='cartTipContainer aboveCart' v-if="icart.discount_notice && icart.discount_notice.note">
							<span>{{icart.discount_notice.note}}</span>
						</div>
					</div>
					<!-- 购物车弹出层 -->
					<van-popup v-model="popupCart" position="bottom" class="popup-cart" round>
						<div class="popup-container">
							<div class="explain" v-if="cartSendCondition > 0">
								还差<span class="c-danger">{{cartSendCondition}}</span>{{Lang.dollarSignCn}}起送（按优惠后金额计算）
							</div>
							<div class="title van-hairline--bottom">
								<div class="cart-num">共{{icart.num}}件商品</div>
								<div class="coupon-wrap" @click="onCollectCoupon" v-if="0">
									<div class="coupon-icon bg-img"></div>
									领券
								</div>
								<div class="clear" @click='onTurncateCart'>清空</div>
							</div>
							<div class="goods-wrap">
								<div class="cart-goods-list ">
									<template v-for="item in icart.data">
										<template v-for="(option, index) in item">
											<div class='cart-goos-item ' v-if="option.goods_id != '88888'">
												<div class="goods-thumb ">
													<img class="img-100" :src="option.thumb" />
												</div>
												<div class="goods-title">{{option.title}}</div>
												<div class="tag-line" v-if="option.discount_num > 0">
													<div class="label-wrap van-hairline--surround">
														特价
														<div class="gap-line"></div>
														<span>限{{option.discount_num}}份</span>
													</div>
												</div>
												<div class="price-line">
													<div class="price-wrap">
														<div class="current">
															<span>{{Lang.dollarSign}}</span>
															<span class="price">{{option.total_discount_price}}</span>
														</div>
														<div class="original" v-if="option.total_price > option.total_discount_price">
															<span>{{Lang.dollarSign}}</span>
															<span>{{option.total_price}}</span>
														</div>
													</div>
													<div class="cartbtn">
														<goods-handle
																:goods="option"
																from="cart"
																:optionId="index"
																:type="from">
														</goods-handle>
													</div>
												</div>
											</div>
										</template>
									</template>
								</div>
								<template v-for="item in icart.data">
									<template v-for="(option, index) in item">
										<div class="pack-fee" v-if="option.goods_id == '88888'">
											<div class="fee-title">{{option.title}}</div>
											<div class="package-text">另需</div>
											<div class="price">
												<span class="price-tag">{{Lang.dollarSign}}</span>{{option.total_discount_price}}
											</div>
										</div>
									</template>
								</template>
							</div>
						</div>
					</van-popup>
				</template>
				<template v-else>
					<div class="rest" v-if="istore.is_rest == 1">本店打烊啦</div>
				</template>
			</template>
			<template v-else>
				<div class="cart mall-cart">
					<div class='cart-tips'>
						<div class='mallcart-empty' v-if="cartTotalNum == 0">
							购物车空空如也~~
						</div>
						<div class="mallcart-noempty" v-else-if="cartTotalNum > 0">
							<div class="cart-price">
								<span class="font-12">合计</span>
								<span class="font-12">{{Lang.dollarSign}}</span><span class="font-bold">{{cartTotalPrice}}</span>
							</div>
							<div class="font-12 margin-10-l">
								共
								<span>{{cartTotalNum}}</span>
								个
							</div>
						</div>
					</div>
					<div class='cart-btns'>
						<div class='cart-btn-unavail'>
							<div class="inner" v-if="!cartTotalNum || cartTotalNum == 0">{{Lang.dollarSign}}{{cartSendCondition}}起送</div>
							<div class="inner" v-else-if="cartSendCondition > 0">差{{Lang.dollarSign}}{{cartSendCondition}}起送</div>
						</div>
						<div @click="onSubmit()" class="cart-btn-confirm" v-if="cartSendCondition <= 0">
							<div class="inner">去购物车</div>
						</div>
					</div>
				</div>
			</template>
		</template>
	</div>
</template>
<script>
	import {mapState, mapMutations} from 'vuex'
	import GoodsHandle from '@/components/goodsHandle'
	export default {
		name: 'StoreCart',
		data() {
			return {
				IN_MALL: false,
				popupCart: false,
				toUrl: '',
			}
		},
		props:{
			show: Number | Boolean,
			from: String,
			table_category: Object,
			table: Object,
			pindan_id: Number,
			ifrom: {
				type: String,
				default() {
					return 'store'
				}
			}
		},
		components: {
			GoodsHandle
		},
		watch: {
			cart_animate() {
				if(this.cart_animate) {
					let cart_icon = this.$refs.cart_icon;
					cart_icon.style.transform = "scale(1.2)"
					setTimeout(() => {
						cart_icon.style.transform = "scale(1)"
						this.replaceState({key: 'cart_animate', val: false});
					}, 200)
				}
			}
		},
		computed: {
			...mapState([
				'istore', 'icart', 'cart_animate'
			]),

			cartTotalNum: function (){
				return this.icart.num;
			},

			cartTotalPrice: function (){
				if(this.from == 'table' || this.from == 'reserve') {
					return (this.icart.price).toFixed(2);
				} else {
					return (this.icart.price + this.icart.box_price).toFixed(2);
				}
			},

			cartSendCondition: function (){
				if(!this.from) {
					if(!this.icart.num) {
						return this.istore.send_price;
					} else {
						return (this.istore.send_price - this.icart.price - this.icart.box_price).toFixed(2);
					}
				} else if(this.from == 'reserve') {
					if(!this.icart.num) {
						return this.table_category.limit_price;
					} else {
						return (this.table_category.limit_price - this.icart.price).toFixed(2);
					}
				} else if(this.from == 'table') {
					if(this.icart.num) {
						return 0;
					}
				}

			}
		},

		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart', 'replaceState'
			]),

			onTurncateCart: function(){
				this.$dialog.confirm({
					title: '温馨提示',
					message: '确定清除购物车吗?'
				}).then(() => {
					this.util.request({
						url: 'wmall/store/goods/truncate',
						data: {
							sid: this.istore.id
						}
					}).then((res) => {
						this.popupCart = false;
						this.replaceCart({});
					});
				});
				return;
			},
			init() {
				this.toUrl = this.util.getUrl({'path': '/pages/order/create', query: {sid: this.istore.id}});
				if(this.from == 'reserve') {
					this.toUrl = this.util.getUrl({'path': '/tangshi/pages/reserve/create', query: {sid: this.istore.id}});
				}
			},
			onSubmit() {
				if(this.icart.is_category_limit == 1) {
					this.$dialog.alert({
						title: '温馨提示',
						message: this.icart.category_limit_cn
					});
					return false;
				}
				if(this.IN_MALL) {
					this.$router.push(this.util.getUrl({'path': '/mall/pages/cart/index', query: {}}));
					return false;
				}
				if(this.pindan_id > 0) {
					this.$router.replace(this.util.getUrl({'path': '/core/pages/store/pindan', query: {sid: this.istore.id, pindan_id: this.pindan_id}}));
				} else {
					var url = '/pages/order/create';
					if(this.ifrom == 'shop') {
						url = '/shop/pages/cart/index'
					}
					this.$router.push(this.util.getUrl({path: url, query: {sid: this.istore.id, is_buysvip: this.icart.is_buysvip}}));
				}
			},
			onSelectPindan: function(){
				var that = this;
				if(that.from == 'table' || that.from == 'reserve') {
					return false;
				}
				if(that.pindan_id > 0 || that.icart.pindan_id == 0 || !that.icart.pindan_id) {
					return false;
				}
				that.$dialog.confirm({
					title: '温馨提示',
					message: '您正在参与拼单中，单独购买请先放弃拼单',
					confirmButtonText: '放弃拼单',
					cancelButtonText: '继续拼单'
				}).then(() => {
					that.util.request({
						url: 'wmall/store/goods/giveupPindan',
						data: {
							sid: that.icart.sid,
							cart_id: that.icart.id
						}
					}).then((res) => {
						var pindan_id = 0;
						that.$emit('selectPinadan', pindan_id);
					});
				}).catch(() => {
					var pindan_id = that.icart.pindan_id;
					that.$emit('selectPinadan', pindan_id);
				});
				return;
			},

			onCollectCoupon() {
				this.popupCart = false;
				this.$emit('onCollectCoupon');
			}

		},

		mounted() {
			//console.log('---');
			//console.log(this.from);
			//this.init()
			window.addEventListener('setItem', ()=> {
				this.IN_MALL = this.util.getStorage('IN_MALL');
			});
		},
		updated() {
			this.onSelectPindan();
		}

	}

</script>


<style>
	/* 购物车 */
	#store-cart .cartTipContainer{
		width: 100%;
		position: absolute;
		bottom: 50px;
		left: 0;
		z-index: 1;
		background: #fff1d0;
		text-align: center;
		height: 25px;
		line-height: 25px;
		display: flex;
		letter-spacing: .15px;
		align-items: center;
		justify-content: center;
		font-size: 11px;
	}
	#store-cart .cart{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50px;
		background-color: #313131;
		z-index: 10000;
	}
	#store-cart .cart .cart-tips{
		color: #999;
		padding-left: 12px;
	}
	#store-cart .mall-cart .cart-tips{
		height: 100%;
	}
	#store-cart .mall-cart .cart-tips>div{
		height: 100%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
		color: #fff;
	}
	#store-cart .cart .cart-tips .cart-icon{
		float: left;
		position: relative;
		z-index: 10;
		top: -18px;
		margin-right: 10px;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		text-align: center;
		background-color: #313131;
		color: #fff;
		line-height: 50px;
		border: 5px solid #444;
		transition: transform .2s;
	}
	#store-cart .cart .cart-tips .cart-icon.active{
		background-color: #ff2d4b;
		color: #fff;
	}
	#store-cart .cart .cart-tips .cart-icon .van-icon{
		display: block;
		font-size: 25px;
		margin-top: 7px;
	}
	#store-cart .cart .cart-tips .cart-icon .cart-num{
		border-radius: 15px;
		background-color: #ff2d4b;
		min-width: 15px;
		height: 15px;
		padding: 0px 3px;
		line-height: 15px;
		font-size: 10px;
		text-align: center;
		position: absolute;
		top: -7px;
		right: -7px;
		color: #fff;
	}
	#store-cart .cart .cart-tips .cart-empty{
		line-height: 50px;
		font-size: 13px;
		color: #888;
	}
	#store-cart .cart .cart-tips .cart-noempty .cart-price{
		font-size: 20px;
		margin-left: 4px;
		color: #fff;
		line-height: 34px;
	}
	#store-cart .cart .cart-tips .cart-noempty .cart-shipping{
		font-size: 12px;
		margin-left: 4px;
		margin-top: -5px;
	}
	#store-cart .cart .cart-btns{
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
	}
	#store-cart .cart .cart-btns .cart-btn-unavail .inner{
		background-color: #2A2A2A;
		color: #999;
	}
	#store-cart .cart .cart-btns .cart-btn-unavail .inner, .cart .cart-btns .cart-btn-confirm .inner{
		width: 110px;
		height: 100%;
		line-height: 50px;
		font-size: 16px;
		text-align: center;
		font-weight: bold;
	}
	#store-cart .cart .cart-btns .cart-btn-confirm .inner{
		background-color: #ff2d4b;
		color: #fff;
	}
	/*打烊了*/
	#store-cart .rest{
		position: fixed;
		bottom: 0;
		z-index: 20000;
		text-align: center;
		width: 100%;
		height: 50px;
		line-height: 50px;
		background-color: #343434;
		color: #fff;
		font-size: 14px;
	}
	/*新购物车弹出层*/
	#store-cart .popup-cart .popup-container{
		padding-bottom: 70px;
	}
	#store-cart .popup-cart .explain{
		width: 100%;
		height: 33px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: PingFangSC-Regular;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		background-color: #fef6de;
		font-size: 12px;
	}
	#store-cart .popup-cart .title{
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 44px;
		padding: 12px;
		background-color: #fff;
	}
	#store-cart .popup-cart .cart-num{
		flex: 1;
		font-family: PingFangSC-Medium;
		font-size: 18px;
		color: #333;
		letter-spacing: 2px;
	}
	#store-cart .popup-cart .coupon-wrap{
		margin-right: 24px;
		height: 100%;
		position: relative;
		display: flex;
		align-items: center;
		font-family: PingFangSC-Regular;
		font-size: 12px;
		color: #ff4454;
	}
	#store-cart .popup-cart .coupon-wrap .coupon-icon{
		margin-right: 3px;
		width: 14px;
		height: 14px;
		background-image: url('https://gw.alicdn.com/tfs/TB1nPjlTVT7gK0jSZFpXXaTkpXa-42-42.png');
		background-repeat: no-repeat;
		background-size: contain;
	}
	#store-cart .popup-cart .clear{
		font-family: PingFangSC-Regular;
		color: #7f7f7f;
		font-size: 12px;
	}
	#store-cart .popup-cart .cart-goods-list{
		padding: 0 12px;
		background-color: #fff;
	}
	#store-cart .popup-cart .cart-goos-item {
		position: relative;
		width: 100%;
		min-height: 84px;
		padding: 9px 0 9px 72px;
		background-color: #fff;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#store-cart .popup-cart .cart-goos-item .goods-thumb {
		position: absolute;
		top: 9px;
		left: 0;
		width: 65px;
		height: 65px;
		border-radius: 6px;
		overflow: hidden;
		font-size: 0;
	}
	#store-cart .popup-cart .cart-goos-item .goods-title{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-family: PingFangSC-Medium;
		font-size: 13px;
		line-height: 16px;
		color: rgba(0, 0, 0, .8);
	}
	#store-cart .popup-cart .cart-goos-item .label-wrap {
		position: relative;
		white-space: nowrap;
		box-sizing: border-box;
		height: 16px;
		display: inline-flex;
		align-items: center;
		padding: 0 4px;
		font-family: PingFangSC-Medium;
		font-size: 12px;
		line-height: 16px;
		color: #ff2d4b;
	}
	#store-cart .popup-cart .cart-goos-item .gap-line {
		margin: 4px;
		width: 1px;
		height: 8px;
		background-color: #ffc9c1;
	}
	#store-cart .popup-cart .cart-goos-item .label-wrap:after{
		border-color: #ffc9c1;
		border-radius: 3px;
	}
	#store-cart .popup-cart .cart-goos-item .price-line{
		width: 100%;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
	}
	#store-cart .popup-cart .cart-goos-item .price-wrap{
		display: flex;
		align-items: flex-end;
	}
	#store-cart .popup-cart .cart-goos-item .current{
		display: flex;
		align-items: flex-end;
		font-family: PingFangSC-Medium;
		font-size: 12px;
		color: #ff2d4b;
	}
	#store-cart .popup-cart .cart-goos-item .current .price{
		font-family: PingFangSC-Medium;
		font-size: 18px;
	}
	#store-cart .popup-cart .cart-goos-item .original{
		display: flex;
		margin-left: 4px;
		font-size: 12px;
		color: #7f7f7f;
		text-decoration: line-through;
	}
	#store-cart .popup-cart .goods-wrap{
		max-height: 432px;
		overflow: scroll;
		background-color: #f5f5f5;
	}
	#store-cart .popup-cart .pack-fee{
		margin-top: 8px;
		width: 100%;
		height: 48px;
		padding-left: 12px;
		background-color: #fff;
		display: flex;
		align-items: center;
	}
	#store-cart .popup-cart .pack-fee .fee-title{
		font-size: 14px;
		margin-right: 26px;
	}
	#store-cart .popup-cart .pack-fee .package-text{
		font-size: 12px;
		color: #7f7f7f;
	}
	#store-cart .popup-cart .pack-fee .price{
		margin: 0 4px;
		font-size: 16px;
		font-weight: 500;
		color: #ff2d4b;
	}
	#store-cart .popup-cart .pack-fee .price-tag{
		font-size: 12px;
	}
</style>



