<template>
	<div id="store-activity">
		<van-popup v-model="popupShow" class="popup-activity" position="bottom" :close-on-click-overlay="true" @click-overlay="onClose" round>
			<div class="popup-header">
				{{store.title}}
				<div class="icon icon-close" @click="onClose"></div>
			</div>
			<div class="popup-content">
				<template v-if="store.coupons.status == 1 || store.svip.status == 1 || store.vipExchange.id > 0 || store.mealExchange.id > 0">
					<div class="headline">红包</div>
					<div class="redpacket-list">
						<div class="redpacket-group" v-if="store.coupons.status == 1">
							<div class="group-title">{{store.coupons.title}}</div>
							<div class="group-desc">总共{{store.coupons.price}}{{Lang.dollarSignCn}}优惠券</div>
							<div class="group">
								<div class="redpacket-item" v-for="(item, index) in store.coupons.coupons" :key="index">
									<div class="detail">
										<div class="amount">
											<div class="dollar">{{Lang.dollarSign}}</div>
											<div class="price">{{item.discount}}</div>
										</div>
										<div class="info">
											<div class="top">
												<div class="label">店铺</div>
												<div class="type">{{item.title}}</div>
											</div>
											<div class="middle">
												<span>满{{Lang.dollarSign}}{{item.condition}}可用,</span>
												领取后{{item.use_days_limit}}天内有效
											</div>
											<div class="bottom">
												<span>全部商品可用</span>
											</div>
										</div>
									</div>
									<div class="received" v-if="item.isget == 1">
										<img src="https://cube.elemecdn.com/c/4c/307f8ee6b361ff6658d8df1a9eb4epng.png" />
									</div>
									<div class="status" v-else>
										<div class="btn" @click="onCouponCollect">领取</div>
									</div>
								</div>
							</div>
						</div>
						<div class="redpacket-wrap">
							<div class="svip-item" v-if="store.svip.status == 1">
								<div class="detail">
									<div class="amount">
										<div class="dollar">{{Lang.dollarSign}}</div>
										<div class="price">{{store.svip.store_redpacket.discount}}</div>
									</div>
									<div class="info">
										<div class="top">
											<span class="svip-icon"></span>
											超级会员店铺红包
										</div>
										<div class="bottom">
											<span>{{store.svip.store_redpacket.condition > 0 ? '满' + store.svip.store_redpacket.condition + Lang.dollarSignCn : '无门槛'}}</span>
											<span>领取后{{store.svip.store_redpacket.use_days_limit}}天内有效</span>
										</div>
									</div>
								</div>
								<div class="status">
									<template v-if="0">
										<div class="coupon-amount">
											1个会员红包
										</div>
										<div class="coupon-amount">
											<span>2</span>个奖励金
										</div>
									</template>
									<template v-if="store.svip.member_status == 1">
										<div class="btn" v-if="store.svip.svip_redpacket.take_status == 'take'" @click="onConfirmSvipExchange">领取</div>
										<div class="btn" v-else-if="store.svip.svip_redpacket.take_status == 'exchange'" @click="onSvipExchange">兑换</div>
									</template>
									<template v-else>
										<div class="btn" @click="util.jsUrl('package/pages/svip/purchase')">去开通</div>
									</template>
								</div>
							</div>
							<div class="redpacket-item" v-if="store.vipExchange.id > 0">
								<div class="detail">
									<div class="amount">
										<div class="dollar">{{Lang.dollarSign}}</div>
										<div class="price">{{store.vipExchange.discount}}</div>
									</div>
									<div class="info">
										<div class="top">
											<div class="label">店铺</div>
											<div class="type">大额会员红包</div>
										</div>
										<div class="middle">
											<span>无门槛</span>
										</div>
										<div class="bottom">
											<span>全部商品可用</span>
										</div>
									</div>
								</div>
								<div class="status" v-if="store.vipExchange.member_status == 1">
									<div class="coupon-amount">需1个会员红包</div>
									<div class="btn" @click="onExchangeVipRedpacket">兑换</div>
								</div>
								<div class="status" v-else>
									<div class="btn" @click="util.jsUrl('/plugin/pages/vipRedpacket/index')">去开通</div>
								</div>
							</div>
							<div class="redpacket-item" v-if="store.mealExchange.id > 0">
								<div class="detail">
									<div class="amount">
										<div class="dollar">{{Lang.dollarSign}}</div>
										<div class="price">{{store.mealExchange.discount}}</div>
									</div>
									<div class="info">
										<div class="top">
											<div class="label">店铺</div>
											<div class="type">大额套餐红包</div>
										</div>
										<div class="middle">
											<span>无门槛</span>
										</div>
										<div class="bottom">
											<span>全部商品可用</span>
										</div>
									</div>
								</div>
								<div class="status" v-if="store.mealExchange.meal_status == 1">
									<div class="coupon-amount">需1个套餐红包</div>
									<div class="btn" @click="onMealExchange">兑换</div>
								</div>
								<div class="status" v-else>
									<div class="btn" @click="util.jsUrl('/package/pages/mealRedpacket/meal')">去购买</div>
								</div>
							</div>
						</div>
					</div>
				</template>
				<template v-if="store.activity.length > 0">
					<div class="headline">优惠</div>
					<div class="activity-list">
						<template v-for="(activityItem, activityIndex) in store.activity">
							<div class="activity-item" v-if="activityItem.activity && activityItem.activity.length > 0">
								<div class="activity-title">{{activityItem.title}}</div>
								<div class="activity" v-for="(item, index) in activityItem.activity" :key="index">
									<div class="label">{{activityItem.label}}</div>
									<div class="msg">{{item}}</div>
								</div>
								<div class="guang" v-if="0">
									<span>去逛逛</span>
									<span class="icon icon-right font-12 c-gray"></span>
								</div>
							</div>
						</template>

					</div>
				</template>
				<template v-if="store.service.length > 0">
					<div class="headline">商家服务</div>
					<div class="panel">
						<div class="service-activity" v-for="(item, index) in store.service" :key="index">
							<div class="service-label-container">
								<div class="service-label-border"></div>
								<span class="service-label">{{item.label}}</span>
							</div>
							<span class="msg">{{item.desc}}</span>
						</div>
					</div>
				</template>
				<div class="headline">公告</div>
				<div class="panel">
					<div class="notice">{{store.notice}}</div>
				</div>
			</div>
		</van-popup>

		<van-popup v-model="popup.svipExchange" class="popup-svip-exchange" v-if="store.svip">
			<div class="popup-svipRedpacket">
				<div class="svipRedpacket-wrap">
					<div class="svipRedpacket-title">兑换{{Lang.dollarSign}}{{store.svip.store_redpacket.discount}}店铺专享红包</div>
					<div class="margin-10-tb">
						<span class="font-14 c-gray">本次兑换将消耗<span class="c-danger">1个</span>「{{Lang.dollarSign}}{{store.svip.svip_redpacket.discount}}会员红包」是否兑换？</span>
					</div>
					<div class="exchange-svipRedpacket">
						<div class="exchange-start">
							<span class="price"><span>{{Lang.dollarSign}}</span>{{store.svip.svip_redpacket.discount}}</span>
							<span class="font-12">会员专享</span>
						</div>
						<div class="exchange-course">
							<img class="img-100" src="data:image/svg+xml;base64," alt="">
						</div>
						<div class="exchange-end">
							<span class="exchange-end-price">
								<span>{{Lang.dollarSign}}</span>
								{{store.svip.store_redpacket.discount}}
							</span>
							<span class="font-12">{{store.svip.store_redpacket.title}}</span>
						</div>
					</div>
					<div class="svipRedpacket-button">
						<van-button size="normal" class="font-16" block @click="onTogglePopup('svipExchange')">放弃优惠</van-button>
						<van-button size="normal" class="bg-danger font-16" block @click="onConfirmSvipExchange">确认兑换</van-button>
					</div>
				</div>
			</div>
		</van-popup>

		<!--会员红包升级弹出层-->
		<van-popup v-model="popup.vipExchange" class="popup-vip-exchange">
			<div class="vip-title text-center font-16 font-bold">会员红包金额升级</div>
			<div class="line-12 margin-15-tb">当前还有{{store.vipExchange.redpacket_num_avaliable_notstore}}张尊享会员红包，升级需要消耗1张</div>
			<div class="redpacket">
				<div class="from">
					<div class="discount">{{Lang.dollarSign}} <span class="font-20">{{vipRedpacketActive.discount}}</span></div>
					<div class="font-12 padding-10-tb text-center">会员红包</div>
					<div class="line"></div>
				</div>
				<i class="icon icon-forwardfill"></i>
				<div class="to">
					<div class="discount">{{Lang.dollarSign}}<span>{{store.vipExchange.discount}}</span></div>
					<div class="logo">
						<img class="img-100" :src="store.vipExchange.logo" alt="">
					</div>
					<div class="redpacket-title ellipsis">{{store.vipExchange.title}}</div>
				</div>
			</div>
			<div class="btn-exchange" @click="onVipExchangeSubmit">确认升级</div>
			<div class="close" @click="onTogglePopup('vipExchange')">
				<i class="icon icon-close font-20"></i>
			</div>
		</van-popup>

		<!--套餐红包兑换-->
		<van-popup class="popup-meal-exchange" v-model="popup.mealExchange" position="bottom" round>
			<div class="popup-title van-hairline--bottom">请选择一个可用红包进行兑换</div>
			<div class="popup-container">
				<div class="redpacket-list">
					<van-row gutter="10">
						<van-col span="12" class="margin-10-b" v-for="(item, index) in store.mealRedpackets" :key="index">
							<div class="redpacket-item" :class="{active: item.id == selectedMealRedpacketId, used: (item.status > 1 || item.sid > 0 || item.scene == 'paotui')}" @click="onSelectMealRedpacket(item)">
								<div class="top">
									<div class="flex-lr">
										<div class="name">{{item.name}}</div>
										<div class="price color-main">
											{{Lang.dollarSign}}<span class="font-20">{{item.discount}}</span>
										</div>
									</div>
									<div class="flex-lr margin-10-tb">
										<div class="useday-limit">有效期: {{item.use_days_limit}}天</div>
										<p class="color-main" v-if="item.condition > 0">满{{item.condition}}{{Lang.dollarSignCn}}</p>
										<p class="color-main" v-else>无门槛</p>
									</div>
								</div>
								<div class="divide">
									<div class="divide-line"></div>
								</div>
								<div class="bottom" v-if="!store.mealExchange.meal_status == 1">{{item.endtime_cn}}</div>
								<div class="bottom" v-else>购买后即可享受优惠</div>
							</div>
						</van-col>
					</van-row>
				</div>
			</div>
			<div class="popup-confirm van-hairline--top text-center c-danger" @click="onMealExchangeSubmit()">立即升级</div>
		</van-popup>
	</div>
</template>

<script>
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		props:{
			show: {
				type: Boolean,
				default() {
					return false
				}
			},
			sid: {
				type: String,
				default() {
					return 0
				}
			}
		},
		data() {
			return {
				store: {
					title: '',
					coupons: {},
					svip: {
						svip_redpacket: {},
						store_redpacket: {}
					},
					vipExchange: {},
					mealExchange: {},
					mealRedpackets: [],
					activity: [],
					service: []
				},
				vipRedpacketActive: {},
				selectedMealRedpacketId: 0,
				popup: {
					svipExchange: false,
					vipExchange: false,
					mealExchange: false,
				}
			}
		},
		computed: {
			popupShow: {
				get(){
					return this.show
				},
				set(val){}
			}
		},
		methods: {
			onClose() {
				this.$emit('onClose');
				return;
			},

			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
			},

			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/store/goods/activity',
					data: {
						sid : that.sid
					},
					success: (res) => {
						that.store = that.util.extend(that.store, res.store);
					},
					fail: (res) => {

					}
				})
			},

			onCouponCollect() {
				var that = this;
				setData({
					vue: that,
					url: 'wmall/channel/coupon/get',
					data: {
						sid: that.store.id,
					},
					success: (res) => {
						that.util.$toast(res);
						that.onClose();
						that.onLoad();
					},
				})
			},

			onSvipExchange() {
				var that = this;
				that.onClose();
				that.onTogglePopup('svipExchange');
			},

			onConfirmSvipExchange() {
				var that = this;
				var svip_redpacket = that.store.svip.svip_redpacket;
				var take_status = svip_redpacket.take_status;
				var id = svip_redpacket.id;
				if(take_status == 'exchange') {
					id = svip_redpacket.store_redpacket.id;
				}
				setData({
					vue: that,
					url: 'wmall/order/create/exchange',
					data: {
						sid: that.store.id,
						id: id,
						oldid: svip_redpacket.exchange_id,
					},
					success: (res) => {
						var message = '领取成功';
						if(take_status == 'exchange') {
							message = '兑换成功';
							that.onTogglePopup('svipExchange');
						}
						that.util.$toast(message);
						that.onTogglePopup('svipExchange');
						that.onLoad();
					},
					fail: (res) => {
						that.onTogglePopup('svipExchange');
						that.util.$toast(res);
					}
				})
			},

			onExchangeVipRedpacket() {
				var that = this;
				getData({
					vue: that,
					url: 'vipRedpacket/index/exchange',
					data: {
						exchange_id: that.store.vipExchange.id
					},
					success: (result) => {
						that.vipRedpacketActive = that.util.extend(that.vipRedpacketActive, result.redpacket);
						that.onClose();
						that.onTogglePopup('vipExchange');
					},
				})
			},

			onVipExchangeSubmit() {
				var that = this;
				setData({
					vue: that,
					url: 'vipRedpacket/index/exchange',
					data: {
						exchange_id: that.store.vipExchange.id,
						redpacket_id: that.vipRedpacketActive.id
					},
					success: (res) => {
						that.util.$toast(res);
						that.onTogglePopup('vipExchange');
						that.onLoad();
					},
					fail: (res) => {
						that.onTogglePopup('vipExchange');
						that.util.$toast(res);
					}
				})
			},

			onMealExchange() {
				var that = this;
				that.onClose();
				that.onTogglePopup('mealExchange');
			},

			onMealExchangeSubmit() {
				var that = this;
				setData({
					vue: that,
					url: 'mealRedpacket/meal/do_exchange',
					data: {
						redpacket_id: that.selectedMealRedpacketId,
						sid: that.store.id,
						mealRedpacket_id: that.store.mealExchange.redpacketid
					},
					success: (res) => {
						that.util.$toast(res);
						that.onTogglePopup('mealExchange');
						that.onLoad();
					},
					fail: (res) => {
						that.onTogglePopup('mealExchange');
						that.util.$toast(res);
					}
				})
			},

			onSelectMealRedpacket(redpacket) {
				var that = this;
				if(redpacket.scene == 'paotui') {
					that.util.$toast('无法使用跑腿红包进行升级');
					return false;
				}
				if(redpacket.status != 1) {
					that.util.$toast('请选择有效的红包进行升级');
					return false;
				}
				if(redpacket.sid > 0) {
					that.util.$toast('无法使用已兑换的红包进行升级');
					return false;
				}
				that.selectedMealRedpacketId = redpacket.id;
			},
		},
		created() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-activity .popup-activity{
		z-index: 10001!important;
	}
	#store-activity .popup-header{
		position: relative;
		width: 100%;
		height: 11.7333333vw;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 4.8vw;
		font-family: PingFangSC-Medium;
		color: #000;
		overflow-y: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		background-color: #f5f5f5;
	}
	#store-activity .popup-header .icon-close{
		position: absolute;
		z-index: 1;
		right: 15px;
		top: 50%;
		transform: translateY(-50%);
		font-size: 4.8vw;
		color: #7a7a7a;
	}
	#store-activity .popup-content{
		min-height: 100vw;
		max-height: 130vw;
		overflow-y: auto;
		background-color: #f5f5f5;
		padding-bottom: 10.6666667vw;
	}
	#store-activity .popup-content .headline{
		font-family: PingFangSC-Regular;
		font-size: 3.7333333vw;
		color: #333;
		line-height: 5.3333333vw;
		font-weight: 800;
		margin: 6.4vw 0 2.4vw 3.2vw;
	}
	#store-activity .popup-content .headline:first-child{
		margin-top: 0;
	}
	#store-activity .popup-content .redpacket-group{
		margin: 0 3.2vw;
		height: 100%;
		background-image: linear-gradient(90deg,#ff794f,#ff4b33);
		border-radius: 2.1333333vw;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		padding: 3.2vw 0 2.1333333vw;
	}
	#store-activity .popup-content .group-title{
		font-size: 4.2666667vw;
		color: #fff;
		font-weight: 600;
	}
	#store-activity .popup-content .group-desc{
		margin-top: 1.0666667vw;
		color: hsla(0,0%,100%,.5);
		font-size: 3.2vw;
	}
	#store-activity .popup-content .group{
		width: 100%;
		margin-top: 2.1333333vw;
		padding: 0 2.1333333vw;
	}
	#store-activity .popup-content .redpacket-item{
		width: 100%;
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 22.4vw;
		margin-top: 1.6vw;
		background: #fff;
		border-radius: 2.1333333vw;
	}
	#store-activity .popup-content .redpacket-item .detail,
	#store-activity .popup-content .svip-item .detail
	{
		width: 66.6666667vw;
		height: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	#store-activity .popup-content .redpacket-item .detail .amount,
	#store-activity .popup-content .svip-item .detail .amount
	{
		width: 20.2666667vw;
		min-width: 20.2666667vw;
		font-size: 4.2666667vw;
		color: #ff4b33;
		display: flex;
		justify-content: center;
		font-family: PingFangSC-Medium;
	}
	#store-activity .popup-content .redpacket-item .detail .amount .dollar,
	#store-activity .popup-content .svip-item .detail .amount .dollar
	{
		height: 7.7333333vw;
		display: inline-flex;
		align-items: flex-end;
	}
	#store-activity .popup-content .redpacket-item .detail .amount .price,
	#store-activity .popup-content .svip-item .detail .amount .price
	{
		font-size: 8.5333333vw;
		line-height: 8.5333333vw;
	}
	#store-activity .popup-content .redpacket-item .detail .info,
	#store-activity .popup-content .svip-item .detail .info
	{
		display: flex;
		flex-direction: column;
		justify-content: center;
		max-width: 50.6666667vw;
		font-size: 2.9333333vw;
	}
	#store-activity .popup-content .redpacket-item .detail .info .top{
		position: relative;
		font-size: 4vw;
		color: rgba(0,0,0,.9);
		font-weight: 500;
		display: flex;
		align-items: center;
		margin-bottom: 1.0666667vw;
	}
	#store-activity .popup-content .redpacket-item .detail .info .top .label{
		padding: 2px 1.6vw;
		top: 1px;
		color: #ff4b33;
		border-radius: 1.6vw;
		border: 1px solid #ffc9c1;
