<template>
	<div id="poster-popup">
		<van-popup
			v-model="popupStatus"
			position="bottom"
			:close-on-click-overlay="false"
			@click-overlay="onTogglePopup"
		>
			<van-row>
				<van-col span="8">
					<div @click="onChangeZhezhao(true)">
						<img src="static/img/public-share-wx.png" alt="">
						<div class="margin-10-t">微信分享</div>
					</div>
				</van-col>
				<van-col span="8">
					<router-link tag='div' :to="util.getUrl({path: '/plugin/pages/poster/index', query: query})">
						<img src="static/img/poster.png" alt="" >
						<div class="margin-10-t">生成海报</div>
					</router-link>
				</van-col>
				<van-col
					span="8"
					v-clipboard:copy="url"
					v-clipboard:success="onCopy"
					v-clipboard:error="onError"
				>
					<img src="static/img/link.png" alt="">
					<div class="margin-10-t">复制链接</div>
				</van-col>
			</van-row>
			<div class="icon icon-close" @click="onTogglePopup"></div>
		</van-popup>
		<div class="share-zhezhao" @click="onChangeZhezhao(false)" v-if="zhezhaoShow">
			<img src="static/img/share-layer.png" alt="">
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			status: Boolean,
			query: {
				type: Object,
				default() {
					return {
						type: 0,
						id: 0,
					}
				}
			}
		},
		data() {
			return {
				url: window.location.href,
				zhezhaoShow: false,
			}
		},
		computed: {
			popupStatus: {
				get(){
					return this.status
				},
				set(val){}
			}
		},
		methods: {
			onTogglePopup() {
				this.$emit('onTogglePopup');
			},
			onChangeZhezhao(force) {
				if(force) {
					this.onTogglePopup();
				}
				this.zhezhaoShow = !this.zhezhaoShow;
			},
			onCopy(e) {
				this.onTogglePopup();
				this.util.$toast('复制成功')
			},
			onError: function (e) {
				this.util.$toast('复制失败')
			}
		}
	}
</script>

<style>
	#poster-popup{
		position: fixed;
		z-index: 100000;
	}
	#poster-popup .van-col{
		text-align: center;
		font-size: 13px;
		color: #7a7a7a;
		padding: 40px 0 20px;
	}
	#poster-popup .van-popup img{
		width: 60px;
		height: 60px;
	}
	#poster-popup .icon-close{
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 18px;
		color: #7a7a7a;
	}
</style>


