<template>
	<div id="public-footer">
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
		<template v-if="menufooter && menufooter.data">
			<van-tabbar v-model="active" v-if="menufooter.params.navstyle == 0">
				<template v-for="(item, index) in menufooter.data">
					<van-tabbar-item @click="util.jsUrl(item.link)" :style="{color: util.isMenuActive(item.link) ? menufooter.css.iconColorActive : menufooter.css.iconColor}" :info="icart.num" v-if="util.strexists(item.link, 'shop/pages/cart/index') && icart.num > 0">
						<div slot="icon" slot-scope="props" class="icon font-20" :class="item.icon"></div>
						<span :style="{color: util.isMenuActive(item.link) ? menufooter.css.textColorActive : menufooter.css.textColor}" slot-scope="props">{{item.text}}</span>
					</van-tabbar-item>
					<van-tabbar-item @click="util.jsUrl(item.link)" :style="{color: util.isMenuActive(item.link) ? menufooter.css.iconColorActive : menufooter.css.iconColor}" v-else>
						<div slot="icon" slot-scope="props" class="icon font-20" :class="item.icon"></div>
						<span :style="{color: util.isMenuActive(item.link) ? menufooter.css.textColorActive : menufooter.css.textColor}" slot-scope="props">{{item.text}}</span>
					</van-tabbar-item>
				</template>
			</van-tabbar>
			<ul class="tabbar-img van-hairline--top" v-else-if="menufooter.params.navstyle == 1">
				<li @click="util.jsUrl(item.link)" class="tabbar-img-item" v-for="(item, index) in menufooter.data" :key="index">
					<img :src="item.img" alt="">
				</li>
			</ul>
			<ul class="tabbar-img-text van-hairline--top" v-else-if="menufooter.params.navstyle == 2">
				<li @click="util.jsUrl(item.link)" class="tab-item" v-for="(item, index) in menufooter.data" :key="index">
					<img class="tab-img" :src="util.isMenuActive(item.link) ? item.imgSelected : item.img" />
					<div class="tab-text" :style="{color: util.isMenuActive(item.link) ? menufooter.css.textColorActive : menufooter.css.textColor}">{{item.text}}</div>
					<div class="cart-num" v-if="util.strexists(item.link, 'shop/pages/cart/index') && icart.num > 0">{{icart.num}}</div>
					<div class="cart-num" v-if="util.strexists(item.link, 'mall/pages/cart/index') && allCartNum > 0">{{allCartNum}}</div>
				</li>
			</ul>
		</template>
		<failed-tips v-if="showFailedTips" :failedTips="failedTips"></failed-tips>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	export default {
		name: 'PublicFooter',
		props: {
			preLoading: {
				type: Boolean || Number
			},
			menufooter: Object,
			failedTips: {},
			showFailedTips: false
		},
		data() {
			return {
				active: 0
			}
		},

		computed: {
			...mapState([
				'icart', 'allcart'
			]),
			allCartNum: function() {
				var num = 0;
				var allcart = this.allcart;
				if(allcart) {
					for(var sid in allcart) {
						for(var i in allcart[sid]['cart']['data']) {
							for(var j in allcart[sid]['cart']['data'][i]) {
								num += allcart[sid]['cart']['data'][i][j]['num'];
							}
						}
					}
				}
				return num;
			}
		},
	}
</script>

<style>
	#public-footer{
		position: absolute;
		z-index: 100;
	}
	#public-footer .van-icon{
		font-size: 20px;
	}
	#public-footer .van-tabbar-item--active {
	    color: #ff2d4b;
	}
	#public-footer .tabbar-img{
		position: fixed;
		right: 0;
		bottom: 0;
		left: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		align-item: center;
		justify-content: center;
		flex-flow: row nowrap;
	}
	#public-footer .tabbar-img .tabbar-img-item{
		width: 100%;
		height: 100%;
		text-align: center;
	}
	#public-footer .tabbar-img .tabbar-img-item img{
		width: 36px;
		height: 36px;
		margin-top: 7px;
	}
	#public-footer .tabbar-img-text{
		position: fixed;
		right: 0;
		bottom: 0;
		left: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		align-item: center;
		justify-content: center;
		flex-flow: row nowrap;
	}
	#public-footer .tabbar-img-text .tab-item{
		position: relative;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		flex: 1;
	}
	#public-footer .tabbar-img-text .tab-item .tab-img{
		width: 24px;
		height: 24px;
	}
	#public-footer .tabbar-img-text .tab-item .tab-text{
		font-size: 12px;
		color: #646566;
		margin-top: 2px;
	}
	#public-footer .cart-num{
		position: absolute;
		right: 30px;
		top: 2px;
		color: #fff;
		font-size: 12px;
		font-weight: 500;
		font-family: PingFang SC,Helvetica Neue,Arial,sans-serif;
		text-align: center;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		padding: 0 3px;
		min-width: 16px;
		line-height: 14px;
		border: 1px solid #fff;
		border-radius: 16px;
		background-color: #ff2d4b;
		transform: translateX(50%);
		transform-origin: 100%;
	}
</style>



