<template>
	<div id="store-header-new" v-if="newStyle">
		<div class="store-bar">
			<div class="bar-left" @click="onBack" v-if="back">
				<span class="icon icon-left"></span>
			</div>
			<div class="bar-right">
				<div class="input" @click="util.jsUrl('/core/pages/store/search', {sid: store.id, pindan_id: pindan_id})">
					<span class="icon icon-search"></span>
					<span>请输入商品名</span>
				</div>
				<div class="icon icon-search" @click="util.jsUrl('/core/pages/store/search', {sid: store.id, pindan_id: pindan_id})"></div>
				<div class="icon icon-comment" @click="util.jsUrl('/plugin/pages/kefu/chat', {relation: 'member2clerk', kefuopenid: store.id, kefuunionid: store.id})" v-if="store.show_kefu == 1 && 0"></div>
				<div :class="{icon: 1, 'icon-favorite': !store.is_favorite, 'icon-favoritesfilling': store.is_favorite}" @click="onFavor(store.id)"></div>
				<div class="icon icon-more" @click="onTogglePopup('more')"></div>
			</div>
		</div>
		<div class="store-sign" >
			<img :src="store.data.shopSign" v-if="store.data && store.data.shopSign" />
		</div>
		<div class="store-info">
			<div class="store-logo">
				<img :src="store.logo" class="img-100">
			</div>
			<div class="store-title">{{store.title}}</div>
			<div class="flex font-12 c-gray margin-10-t">
				<div class="icon icon-favorfill c-danger font-12"></div>
				<div>{{store.score}}</div>
				<div class="margin-10-lr">月售{{store.sailed}}</div>
				<div>配送约{{store.delivery_time}}分钟</div>
			</div>
			<div class="ticket" v-if="(store.svip && store.svip.status == 1) || (store.coupon)">
				<div class="svip" v-if="store.svip && store.svip.status == 1">
					<div class="label">
						<div class="icon icon-crownfill"></div>
					</div>
					<div class="flex padding-5-lr">
						<div>{{Lang.dollarSign}}</div>
						<div class="font-16 font-bold">{{store.svip.store_redpacket.discount}}</div>
						<div class="margin-5-l">{{store.svip.store_redpacket.condition > 0 ? '满' + store.svip.store_redpacket.condition + '元' : '无门槛'}}</div>
					</div>
					<div class="exchange" @click="onTogglePopup('svip')">
						{{store.svip.svip_redpacket.take_status == 'exchange' ? '兑' : '领'}}
					</div>
				</div>
				<div class="coupon" v-if="store.coupon">
					<div class="flex padding-10-lr">
						<div class="font-16 font-bold">{{store.coupon.price}}</div>
						<div>{{Lang.dollarSignCn}}</div>
					</div>
					<div class="exchange" @click="onTogglePopup('coupon')">领</div>
				</div>
			</div>
			<div class="activity" v-if="store.activity && store.activity.labels">
				<div class="activity-item" v-for="(item, index) in store.activity.labels" :key="index">
					{{item.title}}
				</div>
			</div>
			<van-row class="notice">
				<van-col span="22">
					<div class="w-100 font-12 c-gray">公告: {{store.notice}}</div>
				</van-col>
				<van-col span="2" class="text-right">
					<span class="icon icon-unfold font-12 c-gray"></span>
				</van-col>
			</van-row>
		</div>

		<van-popup v-model="popup.coupon" class="store-popup-coupon" round position="bottom" :get-container="getContainer">
			<div class="popup-title van-hairline--bottom">代金券</div>
			<div class="popup-content" v-if="store.coupon && store.coupon.coupons">
				<van-row gutter="10">
					<van-col span="12" v-for="(item, index) in store.coupon.coupons" :key="index">
						<div class="coupon-item">
							<van-row gutter="10" class="flex">
								<van-col span="10">
									<div class="card">
										<div>
											<span>{{Lang.dollarSign}}</span>
											<span class="font-18 font-bold">{{item.discount}}</span>
										</div>
										<div class="margin-5-t">满{{item.condition}}{{Lang.dollarSignCn}}</div>
									</div>
								</van-col>
								<van-col span="14">
									<div>{{item.title}}</div>
									<div class="font-12 c-gray margin-5-t">有效期{{item.use_days_limit}}天</div>
								</van-col>
							</van-row>
						</div>
					</van-col>
				</van-row>
				<van-button @click="onCollectCoupon" class="margin-15-t" color="#ff2d4b" block>领取全部</van-button>
			</div>
		</van-popup>
		<van-popup v-model="popup.svip" class="store-popup-svip" round position="bottom" :get-container="getContainer">
			<div class="popup-title van-hairline--bottom">超级会员</div>
			<div class="popup-content">
				<template v-if="store.svip && store.svip.member_status == 1">
					<div class="font-14 font-500">领取店铺红包</div>
					<div class="svip-redpacket">
						<van-row class="flex">
							<van-col span="5">
								<div class="flex">
									<span class="font-12">{{Lang.dollarSign}}</span>
									<span class="font-20 font-bold">{{store.svip.store_redpacket.discount}}</span>
								</div>
							</van-col>
							<van-col span="14">
								<div class="margin-10-b font-14 font-500">店铺红包</div>
								<div>
									{{store.svip.store_redpacket.condition > 0 ? '满' + store.svip.store_redpacket.condition + '元' : '无门槛'}}, 领取后有效期{{store.svip.store_redpacket.use_days_limit}}天
								</div>
							</van-col>
							<van-col span="5">
								<template v-if="store.svip.svip_redpacket.take_status == 'take'">
									<div class="take" @click="onConfirmSvipExchange">领取</div>
								</template>
								<template v-else-if="store.svip.svip_redpacket.take_status == 'exchange'">
									<div class="take" @click="onTogglePopup('svipExchange')">兑换</div>
								</template>
							</van-col>
						</van-row>
					</div>
				</template>
				<template v-else>
					<div class="font-14 font-500">开通超级会员</div>
					<div class="buy-svip">
						<div>
							<div class="flex">
								<div class="padding-10-r border-1px-r">尊享会员</div>
								<div class="padding-10-l">每月送30元红包</div>
							</div>
							<div class="font-16 margin-10-t">
								本单可用
								<span class="c-danger font-18 font-bold">7</span>
								{{Lang.dollarSignCn}}
							</div>
						</div>
						<div class="buy-btn" @click="util.jsUrl('package/pages/svip/purchase')">15元开通</div>
					</div>
				</template>

			</div>
		</van-popup>

		<van-popup v-model="popup.svipExchange" class="popup-svip-exchange" :get-container="getContainer" v-if="store.svip">
			<div class="popup-svipRedpacket">
				<div class="svipRedpacket-wrap">
					<div class="svipRedpacket-title">兑换{{Lang.dollarSign}}{{store.svip.store_redpacket.discount}}店铺专享红包</div>
					<div class="margin-10-tb">
						<span class="font-14 c-gray">本次兑换将消耗<span class="c-danger">1个</span>「{{Lang.dollarSign}}{{store.svip.svip_redpacket.discount}}会员红包」是否兑换？</span>
					</div>
					<div class="exchange-svipRedpacket">
						<div class="exchange-start">
							<span class="price"><span>{{Lang.dollarSign}}</span>{{store.svip.svip_redpacket.discount}}</span>
							<span class="font-12">会员专享</span>
						</div>
						<div class="exchange-course">
							<img class="img-100" src="data:image/svg+xml;base64," alt="">
						</div>
						<div class="exchange-end">
							<span class="exchange-end-price">
								<span>{{Lang.dollarSign}}</span>
								{{store.svip.store_redpacket.discount}}
							</span>
							<span class="font-12">{{store.svip.store_redpacket.title}}</span>
						</div>
					</div>
					<div class="svipRedpacket-button">
						<van-button size="normal" class="font-16" block @click="onTogglePopup('svipExchange')">放弃优惠</van-button>
						<van-button size="normal" class="bg-danger font-16" block @click="onConfirmSvipExchange">确认兑换</van-button>
					</div>
				</div>
			</div>
		</van-popup>

		<van-popup v-model="popup.more" class="popup-more" position="top" :overlay="false">
			<div class="more-item">
				<span class="icon icon-cart"></span>
				<span>购物车</span>
			</div>
			<div class="more-item">
				<span class="icon icon-share1"></span>
				<span>分享商家</span>
			</div>
			<div class="more-item" @click="util.jsUrl('/core/pages/store/report', {sid: store.id})">
				<span class="icon icon-info"></span>
				<span>举报商家</span>
			</div>
		</van-popup>
	</div>
	<div id="store-header" :class="`store-header-${store.headerStyle}`" v-else>
		<div class="store-header-normal" v-if="store.headerStyle == 0">
			<!--店铺招牌-->
			<div class="store-sign">
				<img :src="store.data.shopSign" alt="" v-if="store.data && store.data.shopSign">
			</div>
			<div class="mask-block">
				<img src="static/img/mask-bg.png" alt="">
			</div>
			<!--店铺头部-->
			<div class='header clearfix'>
				<!-- 店铺详情 -->
				<div class='top clearfix'>
					<div class='avatar'>
						<img :src="store.logo">
					</div>
					<div class='campaign'>
						<div class='text' v-if="table">
							<div v-if="table.category">{{table.category.title}}~{{table.title}}号桌</div>
							<div class='fg-line' v-if="store.serve_fee && store.serve_fee.fee > 0"></div>
							<div v-if="store.serve_fee && store.serve_fee.fee > 0">服务费{{store.serve_fee.type == 1 ? Lang.dollarSign + store.serve_fee.fee : store.serve_fee.fee+'% 订单消费'}}</div>
						</div>
						<div class='text' v-else-if="table_category">
							<div>{{table_category.title}}</div>
							<div class='fg-line'></div>
							<div>{{reserveExtra.day}}~{{reserveExtra.time}}</div>
						</div>
						<div class='text' v-else>
							<div>起送{{Lang.dollarSign}}{{store.send_price > 0 ? store.send_price : '0'}}</div>
							<div class='fg-line'></div>
							<div>配送{{Lang.dollarSign}}{{store.delivery_price > 0 ? store.delivery_price : '0'}}</div>
							<div class='fg-line'></div>
							<div>{{store.delivery_time}}分钟</div>
							<div class='allocation-icon' v-if="store.delivery_title">{{store.delivery_title}}</div>
						</div>
						<!-- 公告 -->
						<div class="notice flex-lr">
							<van-icon name="voice" class="c-white font-13 margin-5-r"></van-icon>
							<van-notice-bar class="flex-1" delay="3" background="inherit" color="#ffffff" :scrollable="true" v-if="store.notice">
								{{store.notice}}
							</van-notice-bar>
						</div>
					</div>
					<template v-if="table">
						<div class='collect' @click="onCallServe(store.id, table.id)">
							<van-icon name='notice'></van-icon>
							<div class='text'>呼叫</div>
						</div>
						<router-link class='collect' :to="util.getUrl({path: '/pages/order/index'})">
							<van-icon name='order'></van-icon>
							<div class='text'>订单</div>
						</router-link>
					</template>
					<template v-else>
						<div class='collect' v-if="store.is_favorite == 1" @click="onFavor(store.id)">
							<van-icon name='favoritesfilling'></van-icon>
							<div class='text'>已收藏</div>
						</div>
						<div class='collect' v-else @click="onFavor(store.id)">
							<van-icon name='favorite'></van-icon>
							<div class='text'>收藏</div>
						</div>
					</template>
					<div style="clear:both;"></div>
				</div>
				<!-- 活动栏 -->
				<div class='activity-box clearfix'>
					<template v-if="store.activity && store.activity.num && store.activity.num > 0">
						<van-swipe class="activity-scroll" :autoplay="2000" vertical :show-indicators="false">
							<template v-for="activity in store.activity.items">
								<van-swipe-item>
									<div class="swiper-slide swiper-slide-prev">
										<img :src="'static/img/' + activity.type + '_b.png'" alt="">
										<div>{{activity.title}}</div>
									</div>
								</van-swipe-item>
							</template>
						</van-swipe>
						<div class='more'>
							<router-link :to="util.getUrl({path: '/pages/store/index', query: {sid: store.id}})">
								{{store.activity.num}}个活动
								<van-icon name='xiangyou1'></van-icon>
							</router-link>
						</div>
					</template>
					<div class="interim" v-else>
						<div>起送{{Lang.dollarSign}}{{store.send_price > 0 ? store.send_price : '0'}}</div>
						<div class='fg-line'></div>
						<div>配送{{Lang.dollarSign}}{{store.delivery_price}}</div>
						<div class='fg-line'></div>
						<div>{{store.delivery_time}}分钟</div>
					</div>
				</div>
			</div>
		</div>
		<div class="store-header-card" v-else-if="store.headerStyle == 1">
			<div class="store-sign" v-if="store.data && store.data.shopSign">
				<img :src="store.data.shopSign" alt="" >
			</div>
			<i class="icon icon-left" @click="onBack"></i>
			<i class="icon icon-more" @click="onTogglePopup('more')" v-if="util.isGlala()"></i>
			<div class='header clearfix'>
				<!-- 店铺详情 -->
				<div class='top clearfix'>
					<div class='avatar'>
						<img :src="store.logo">
					</div>
					<div class='campaign'>
						<div class="store-title w-100 ellipsis">{{store.title}}</div>
						<div class='text' v-if="table">
							<div v-if="table.category">{{table.category.title}}~{{table.title}}号桌</div>
							<div class='fg-line' v-if="store.serve_fee && store.serve_fee.fee > 0"></div>
							<div v-if="store.serve_fee && store.serve_fee.fee > 0">服务费{{store.serve_fee.type == 1 ? Lang.dollarSign + store.serve_fee.fee : store.serve_fee.fee+'% 订单消费'}}</div>
						</div>
						<div class='text' v-else-if="table_category">
							<div>{{table_category.title}}</div>
							<div class='fg-line'></div>
							<div>{{reserveExtra.day}}~{{reserveExtra.time}}</div>
						</div>
						<div class='text' v-else>
							<div>起送{{Lang.dollarSign}}{{store.send_price > 0 ? store.send_price : '0'}}</div>
							<div class='fg-line'></div>
							<div>配送{{Lang.dollarSign}}{{store.delivery_price > 0 ? store.delivery_price : '0'}}</div>
							<div class='fg-line'></div>
							<div>{{store.delivery_time}}分钟</div>
							<div class='allocation-icon' v-if="store.delivery_title">{{store.delivery_title}}</div>
						</div>

					</div>
					<div class="collect-wrap">
						<template v-if="table">
							<div class='collect' @click="onCallServe(store.id, table.id)">
								<van-icon name='notice'></van-icon>
								<div class='text'>呼叫</div>
							</div>
							<router-link class='collect' :to="util.getUrl({path: '/pages/order/index'})">
								<van-icon name='order'></van-icon>
								<div class='text'>订单</div>
							</router-link>
						</template>
						<template v-else>
							<div class='collect' v-if="store.is_favorite == 1" @click="onFavor(store.id)">
								<van-icon name='favoritesfilling'></van-icon>
								<div class='text'>已收藏</div>
							</div>
							<div class='collect' v-else @click="onFavor(store.id)">
								<van-icon name='favorite'></van-icon>
								<div class='text'>收藏</div>
							</div>
						</template>
					</div>
					<div style="clear:both;"></div>
				</div>
				<!-- 公告 -->
				<div class="notice flex-lr">
					<van-icon name="voice" class="c-white font-13 margin-5-r"></van-icon>
					<van-notice-bar class="flex-1" delay="3" background="inherit" color="#ffffff" :scrollable="true" v-if="store.notice">
						{{store.notice}}
					</van-notice-bar>
				</div>
				<!-- 活动栏 -->
				<div class='activity-box clearfix'>
					<template v-if="store.activity && store.activity.num && store.activity.num > 0">
						<van-swipe class="activity-scroll" :autoplay="2000" vertical :show-indicators="false">
							<template v-for="activity in store.activity.items">
								<van-swipe-item>
									<div class="swiper-slide swiper-slide-prev">
										<img :src="'static/img/' + activity.type + '_b.png'" alt="">
										<div>{{activity.title}}</div>
									</div>
								</van-swipe-item>
							</template>
						</van-swipe>
						<div class='more'>
							<router-link :to="util.getUrl({path: '/pages/store/index', query: {sid: store.id}})">
								{{store.activity.num}}个活动
								<van-icon name='xiangyou1'></van-icon>
							</router-link>
						</div>
					</template>
					<div class="interim" v-else>
						<div>起送{{Lang.dollarSign}}{{store.send_price > 0 ? store.send_price : '0'}}</div>
						<div class='fg-line'></div>
						<div>配送{{Lang.dollarSign}}{{store.delivery_price}}</div>
						<div class='fg-line'></div>
						<div>{{store.delivery_time}}分钟</div>
					</div>
				</div>
			</div>
		</div>
		<div class="store-header-card2" v-else-if="store.headerStyle == 2">
			<div class="store-sign-2" :style="{'background-image': `url('${store.data.shopSign}')`}"></div>
			<div :class="{'search-bar': 1, 'scrolled': showRedpacketIcon}" :style="{'background-color': `rgba(255,255,255,${searchBarOpacity})`}">
				<div class="icon icon-left" @click="onBack" v-if="back"></div>
				<div class="search" @click="util.jsUrl('/core/pages/store/search', {sid: store.id, pindan_id: pindan_id})">
					<span class="icon icon-search"></span>
					<span class="placeholder">输入商品名称搜索</span>
				</div>
				<div class="rightzone">
					<div class="redpacket" @click.stop="onTogglePopup('activity')">
						<img src="https://cube.elemecdn.com/0/fe/d1a0d62a58d3ad2bc743a4e023afepng.png" />
					</div>
					<div class="favor" @click="onFavor(store.id)">
						<div :class="{'favor-star': 1, 'bg-img': 1, 'favored': store.is_favorite == 1}"></div>
						<span class="favor-text">收藏</span>
					</div>
					<div class="icon icon-more" @click="onTogglePopup('more')" v-if="util.isGlala()"></div>
				</div>
			</div>
			<div class="store-container">
				<div class="store-header">
					<div class="background">
						<div class="placehold"></div>
						<div class="bg"></div>
					</div>
					<div class="bottom-placehold"></div>
					<div class="main-wrapper">
						<div class="main">
							<div class="store-info">
								<div class="info-wrapper">
									<div class="name-wrapper">
										<div class="name">{{store.title}}</div>
									</div>
									<div class="tags-wrapper">
										<div class="delivery-info">
											<span class="delivery-type" v-if="store.delivery_title">{{store.delivery_title}}</span>
											<span class="delivery-time">约{{store.delivery_time}}分钟</span>
										</div>
										<div class="tags">
											<div class="tag-text">
												<span class="dot"></span>起送{{Lang.dollarSign}}{{store.send_price > 0 ? store.send_price : '0'}}
											</div>
											<div class="tag-text">
												<span class="dot"></span>配送{{Lang.dollarSign}}{{store.delivery_price > 0 ? store.delivery_price : '0'}}
											</div>
										</div>
									</div>
								</div>
								<div class="logo-wrapper">
									<img class="logo-decorator" src="https://img.alicdn.com/tfs/TB1T35TM.H1gK0jSZSyXXXtlpXa-66-36.png_310x310Q90s50_.webp" v-if="0"/>
									<img class="store-logo" :src="store.logo" />
								</div>
							</div>
							<div class="notice" v-if="store.notice">
								公告: {{store.notice}}
							</div>
							<div class="coupons-container" v-if="store.svip.status == 1 || (store.coupon)">
								<div class="coupon-list">
									<div class="coupon-item" v-if="store.coupon">
										<div :class="{'coupon': 1, 'received': store.coupon.isget == 1, 'normal': store.coupon.isget == 0}" @click.stop="onTogglePopup('activity')">
											<div class="border-bg"></div>
											<div class="detail">
												<span class="extra-text">共</span>
												<span class="icon">{{Lang.dollarSign}}</span>
												<span class="price">{{store.coupon.price}}</span>
												<span class="msg">红包</span>
											</div>
											<div class="status">
												<div class="circle top-circle"></div>
												<div class="circle bottom-circle"></div>
												<div class="line red"></div>
												<div >{{store.coupon.isget == 1 ? '已领' : '领'}}</div>
											</div>
										</div>
									</div>
									<div class="coupon-item" v-if="store.svip.status == 1">
										<div class="coupon vip" @click.stop="onTogglePopup('activity')">
											<div class="border-bg"></div>
