</template>

<script>
	import storeActivity from '@/components/storeActivity'
	import PublicShare from '@/components/share'
	import {mapState, mapMutations} from 'vuex'
	import {setData} from "@/controller/funcCommon"
	export default {
		components: {
			storeActivity, PublicShare
		},
		props: {
			store: {
				type: Object,
				default() {
					return {
						is_rest: 0,
						activity: {
							num: 0
						},
						headerStyle: 0,
						svip: {},
						data: {}
					}
				}
			},
			table: Object,
			table_category: Object,
			pindan_id: 0,
			showRedpacketIcon: false,
			searchBarOpacity: 0,
			newStyle: {
				type: false,
				default: 0
			}
		},
		data() {
			return {
				back: false,
				more: false,

				popup: {
					more: false,
					svip: false,
					coupon: false,
					svipExchange: false,
					activity: false,
					share: false
				}
			}
		},
		methods: {
			onConfirmSvipExchange() {
				var that = this;
				var svip_redpacket = that.store.svip.svip_redpacket;
				var take_status = svip_redpacket.take_status;
				var id = svip_redpacket.id;
				if(take_status == 'exchange') {
					id = svip_redpacket.store_redpacket.id;
				}
				setData({
					vue: that,
					url: 'wmall/order/create/exchange',
					data: {
						sid: that.store.id,
						id: id,
						oldid: svip_redpacket.exchange_id,
					},
					success: (res) => {
						var message = '领取成功';
						if(take_status == 'exchange') {
							message = '兑换成功';
							that.onTogglePopup('svipExchange');
						}
						that.util.$toast(message);
						that.onTogglePopup('svip');
					}
				})
			},

			onCollectCoupon() {
				this.util.request({
					url: 'wmall/channel/coupon/get',
					data: {
						sid: this.store.id
					},
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.util.$toast('领取失败', '', 1000);
						return false;
					}
					this.util.$toast(result.message, '', 1000);
					this.$set(this.store, 'coupon', false)
					this.onTogglePopup('coupon');
				})
			},

			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
				if(key == 'share' && !this.popup[key]) {
					this.popup['more'] = !this.popup['more'];
				}
			},

			onBack() {
				if(!this.back) {
					return;
				}
				this.$router.back(-1);
			},

			onFavor(sid) {
				if(this.store.is_favorite) {
					var type = 'cancal';
				} else {
					var type = 'star';
				}
				this.util.request({
					url: 'wmall/member/op/favorite',
					data: {
						id: sid,
						type: type
					},
				}).then((res) => {
					let result = res.data.message;
					if(res.data.message.errno == 0) {
						if(type == 'star') {
							this.$toast('添加收藏成功');
							this.store.is_favorite = 1;
						} else {
							this.$toast('取消收藏成功');
							this.store.is_favorite = 0;
						}
					} else {
						this.$toast(res.data.message.message);
					}

				})
			},
			onCallServe(sid, table_id) {
				this.util.request({
					url: 'wmall/store/call/index',
					data: {
						sid: sid,
						table_id: table_id
					},
				}).then((res) => {
					this.$toast(res.data.message.message);
				})
			},

			getContainer() {
				return document.querySelector('#app');
			},
		},

		mounted() {
			if(window.history.length > 1) {
				this.back = true;
			}
		},
		computed: {
			...mapState([
				'reserveExtra'
			]),
		}
	}
</script>

